    // For monthrange tab, omit date range string
    dateRangeString = '';
  }
  if (tab.value === 'daterange') {
    // For daterange tab, omit months and years string
    monthsString = '';
    yearsString = '';
  }
  
  return [dateRangeString, monthsString, yearsString, daysString, timesString]
    .filter(s => s !== '')
    .join(' | ');
    
